import logo from './logo.svg';
import './App.css';
import {useState} from 'react';

import liff from '@line/liff';

liff.init({ liffId: '1655180874-rpzb2gR5' });

const sendAlertIfNotInClient = () => { console.log('sendAlertIfNotInClient') }
const toggleQrCodeReader = () => { console.log('toggleQrCodeReader') }

const sendMessageButton = (Message) => {
  if (!liff.isInClient()) {
    sendAlertIfNotInClient();
  } else {
      liff.sendMessages([{
          'type': 'text',
          'text': Message
      }]).then(function() {
          window.alert('Message sent');
      }).catch(function(error) {
          window.alert('Error sending message: ' + error);
      });
  }
}


function App() {
  const [QRtxt, setQRtxt] = useState('');

  const scanQRCode = () => {
    if (!liff.isInClient()) {
      sendAlertIfNotInClient();
    } else {
      if (liff.scanCode) {
          liff.scanCode().then(result => {
              // e.g. result = { value: "Hello LIFF app!" }
              const stringifiedResult = JSON.stringify(result);
              setQRtxt(result.value);
              document.getElementById('scanQrField').textContent = stringifiedResult;
              toggleQrCodeReader();
          }).catch(err => {
              setQRtxt('')
              document.getElementById('scanQrField').textContent = "scanCode failed!";
          });
      }
    }
  }

  return (
    <div className="App">
      {/* <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header> */}
      <div id="scanQrField" ></div>
      <button onClick={scanQRCode} >Scan QR Code</button>
      <p>{QRtxt}</p>
      { QRtxt && (<button onClick={() => sendMessageButton(QRtxt)} >send Message Button</button>)}
    </div>
  );
}

export default App;
